.joint-viewport {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.joint-paper-background,
.joint-paper-grid,
.joint-paper > svg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
[magnet="true"]:not(.joint-element) {
  cursor: crosshair;
}
[magnet="true"]:not(.joint-element):hover {
  opacity: 0.7;
}
.joint-element {
  cursor: move;
}
.joint-element * {
  user-drag: none;
}
.joint-element .scalable * {
  vector-effect: non-scaling-stroke;
}
.marker-source,
.marker-target {
  vector-effect: non-scaling-stroke;
}
.joint-paper {
  position: relative;
}
.joint-highlight-opacity {
  opacity: 0.3;
}
.joint-link .connection,
.joint-link .connection-wrap {
  fill: none;
}
.marker-vertices {
  opacity: 0;
  cursor: move;
}
.marker-arrowheads {
  opacity: 0;
  cursor: move;
  cursor: -webkit-grab;
  cursor: -moz-grab;
}
.link-tools {
  opacity: 0;
  cursor: pointer;
}
.link-tools .tool-options {
  display: none;
}
.joint-link:hover .link-tools,
.joint-link:hover .marker-arrowheads,
.joint-link:hover .marker-vertices {
  opacity: 1;
}
.marker-vertex-remove {
  cursor: pointer;
  opacity: 0.1;
}
.marker-vertex-group:hover .marker-vertex-remove {
  opacity: 1;
}
.marker-vertex-remove-area {
  opacity: 0.1;
  cursor: pointer;
}
.marker-vertex-group:hover .marker-vertex-remove-area {
  opacity: 1;
}
.joint-element .fobj {
  overflow: hidden;
}
.joint-element .fobj body {
  background-color: transparent;
  margin: 0;
  position: static;
}
.joint-element .fobj div {
  text-align: center;
  vertical-align: middle;
  display: table-cell;
  padding: 0 5px 0 5px;
}
